.projects-heading{
    color: #ffffff;
    text-wrap: nowrap;
    font: 700 30px Manrope, sans-serif;
    text-align: flex;
}

.projects__images{
    display: flex;
}
.projects__images img{
    width: 100%;
    height: 100%;
    align-items: center;
    padding-top: 40px;
    padding-bottom: 40px;
    display:flex;

}

.projects__mobilesppdesign{
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.vertical-yummy{
    writing-mode: vertical-rl;
    text-orientation: sideways;
    transform: rotate(-180deg);


    color: #000000;
    text-wrap: nowrap;
    font: 700 80px Manrope, sans-serif;
    text-align: flex;
 
}



.mobile__apps{
    display: flex;
}
.mobile__apps img{
    width: 100%;
    height: 100%;
    align-items: right;
    padding-left: 230px;
    display:flex;
}