.gpt3__header {
    display: flex;
    flex-direction: row;
}

.gpt3__header-content {
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-direction: column;
    margin-right: 5rem;
}

.gpt3__header-content h1{
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 74px;
    line-height: 90px;
    letter-spacing: -0.04em;

    margin-top: 1.5rem;
    margin-left: 30px;
}

.gpt3__header-content__link {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 28px;
    line-height: 300px;
    color: white;
    display: inline-flex; 
    align-items: center;
    padding-right: 100px;
    margin-left: 30px;
   


}

.gpt3__header-content__arrow img {
    width: 50px;
    height: 24px;
    margin-left: 40px;
    vertical-align: middle;
    animation: arrowMove 2s linear infinite;
}

@keyframes arrowMove {
    0% {
        transform: translateX(0);
    }
    50% {
        transform: translateX(20px); /* Move right */
    }
    100% {
        transform: translateX(0); /* Move back to the original position */
    }
}



@media screen and (max-width: 1050px) {
    .gpt3__header {
        flex-direction: column;
    }

    .gpt3__header-content {
        margin: 0 0 3rem;
    }
}

@media screen and (max-width: 650px) {
    .gpt3__header-content h1 {
        font-size: 48px;
        line-height: 60px;
    }
    .gpt3__header-content h2 {
        font-size: 48px;
        line-height: 60px;
    }

    .gpt3__header-content p {
        font-size: 48px;
        line-height: 60px;
    }
    
    .gpt3__header-content__link{
        flex-direction: column;
        font-size: 48px;
        line-height: 60px;
    }

    .gpt3__header-content__arrow {
        margin: 0;
    }

    .gpt3__header-content__link {
        font-size: 16px;
        line-height: 24px;
    }
    .gpt3__header-content__arrow img {
        width: 40px;
        height: 24px;
        margin-left: 10px;
        vertical-align: middle;
        animation: arrowMove 2s linear infinite;
    }
}

@media screen and (max-width: 490px) {
    .gpt3__header-content h1 {
        font-size: 36px;
        line-height: 30px;
    }
    .gpt3__header-content h2 {
        font-size: 36px;
        line-height: 30px;
    }

    .gpt3__header-content p {
        font-size: 36px;
        line-height: 30px;
    }

    .gpt3__header-content__link{
        font-size: 12px;
        line-height: 54px;
    }
    .gpt3__header-content__arrow img {
        width: 50px;
        height: 24px;
        margin-left: 40px;
        vertical-align: middle;
        animation: arrowMove 2s linear infinite;
    }
}