.divein {
    display: flex;
    flex-direction: column;

}
.gpt3__whatgpt3 {
    display: flex;
    flex-direction: column;
    padding: 2rem;
    background: var(--color-footer);
    border-radius: 50px;
}

.gpt3__whatgpt3-feature {
  display: flex;
}

.gpt3__whatgpt3-feature 
.gpt3__features-container__feature {
  margin: 0;
}

.gpt3__whatgpt3-feature 
.gpt3__features-container_feature-text {
    max-width: 700px;
}



.gpt3__whatgpt3-heading h1 {
    font-size: 40px;
    line-height: 60px;
    font-weight: 800;
    font-family: var(--font-family);
    text-align: center;
    justify-content: center;
    align-items: center;
    display: flex;
    color: #000000;
}

.gpt3__whatgpt3-heading p {
    font-family: var(--font-family);
    font-weight: 300;
    font-size: 20px;
    line-height: 30px;
    margin-top: 2rem;
    text-align: center;
    justify-content: center;
    align-items: center;
    display: flex;
    color: #000000;
    cursor: pointer;
}

  /* Style for the container with padding on the left */
.gpt3__whatgpt3-container {
    text-align: center;
    margin-left: 6rem; 
    margin-right: 6rem;
    display: flex; 
    justify-content: space-between;
    align-items: center; 
    padding-top: 2px;
  }
  
  .gpt3__whatgpt3-container > div {
    flex: 1; 
    flex-direction: row;
    align-items: center;
  }
  
  .chatme{
    align-items: center;
    padding-right: 4rem;
  }
  /* Styling for the chatme section */
  .chatme img {
    width: 80px;
    height: 80px;
  }
  
  .divein__response {
    font-weight: bold;
    font-size: 28px;
    padding-top: 5px;
    text-align: center;
    font-family: var(--font-family);
    color: white;
    line-height: 80px;
  }
  
  .divein__respone-text {
    font-size: 20px;
    padding-top: 10px;
    text-align: center;
    font-family: var(--font-family);
    line-height: 30px;
  }
  
  .quality{
    align-items: center;
    padding-right: 4rem;
  }
  .quality img {
    width: 80px;
    height: 80px;
  }
  
  .divein__quality {
    font-weight: bold;
    font-size: 28px;
    padding-top: 5px;
    text-align: center;
    font-family: var(--font-family);
    line-height: 80px;
    color: white;
  }
  
  .divein__quality-text {
    font-size: 20px;
    padding-top: 10px;
    text-align: center;
    font-family: var(--font-family);
    line-height: 30px;
  }
  
  .scale{
    align-items: center;
  }
  .scale img {
    width: 80px;
    height: 80px;
  }
  
  .divein__scalable {
    font-weight: bold;
    font-size: 28px;
    padding-top: 5px;
    text-align: center;
    font-family: var(--font-family);
    line-height: 80px;
    color: white;
  }
  
  .divein__scalable-text {
    font-size: 20px;
    padding-top: 10px;
    text-align: center;
    font-family: var(--font-family);
    line-height: 30px;
  }
  

  

@media screen and (max-width: 850px) {
    .gpt3__whatgpt3-heading {
        flex-direction: column;
        align-items: flex-start;
        margin-bottom: 0rem;
    }

    .gpt3__whatgpt3-heading p {
        margin-top: 1rem;
    }
}

@media screen and (max-width: 650px) {
    /* Customizing feature component as per needs */
    .gpt3__whatgpt3-feature .gpt3__features-container__feature {
        flex-direction: column;
    }

    /* Customizing feature component as per needs */
    .gpt3__whatgpt3-feature .gpt3__features-container_feature-text {
        margin-top: 0.5rem;
    }

    .gpt3__whatgpt3-heading h1 {
        font-size: 28px;
        line-height: 40px;
    }
}

@media screen and (max-width: 350px) {
    /* Customizing feature component as per needs */
    .gpt3__whatgpt3-container .gpt3__features-container__feature {
        margin: 1rem 0;
        min-width: 100%;
    }
}